@import '../mixins/fix-size';
@import '../mixins/media';

.orderEquipment {
	margin: 34px 10px 0 10px;
	width: 320px;

	@include media(xs) {

	}
	@include media(sm) {
		width: 460px;
	}
	@include media(md) {

	}
	@include media(lg) {

	}

	&__title {
		font-family: 'Proxima Nova', sans-serif;
		font-size: 30px;
		font-weight: bold;
		line-height: 2;
		letter-spacing: 0.6px;
		text-align: center;
		color: #272727;
	}
	
	&__image {
		display: block;
		box-shadow: 2px 4px 10px 0 rgba(0, 0, 0, 0.4);
		background-color: #1d190a;
		border: 0;
		width: 320px;
		height: auto;;

		@include media(xs) {

		}
		@include media(sm) {
			width: 460px;
		}
		@include media(md) {

		}
		@include media(lg) {

		}
	}

	&__price {
		background-color: black;
		font-family: 'Roboto', sans-serif;
		font-size: 18px;
		font-weight: bold;
		letter-spacing: 0.4px;
		text-align: center;
		color: #ffffff;
		padding: 16px 0;
		box-shadow: 2px 4px 10px 0 rgba(0, 0, 0, 0.4);
	}
}