@import '../mixins/fix-size';

.order {
	margin: 27px 12px;
	width: 216px;
	display: flex;
	flex-direction: column;
	align-items: center;

	&__image {
		@include fixSize(160px, 160px);
		border-radius: 50%;
		box-shadow: 2px 4px 10px 0 rgba(0, 0, 0, 0.4);
		background-color: #b8b6bc;
		border: 0;
	}

	&__title {
		font-family: 'Proxima Nova', sans-serif;
		font-size: 24px;
		font-weight: bold;
		line-height: 1.33;
		letter-spacing: 0.5px;
		text-align: center;
		color: #272727;
		margin-top: 21px;
		height: 94px;
		width: 100%;
	}

	&__price {
		margin-top: 12px;
		padding: 10px;
		background-color: #F3D456;
		font-family: 'Roboto', sans-serif;
		font-size: 14px;
		line-height: 1.43;
		letter-spacing: 0.1px;
		text-align: center;
		color: #272727;
		width: 100%;
	}
}