@import "../mixins/media";

////////////////////////////// header info //////////////////////////////

.headerInfo {
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	padding: 70px 10px 16px 10px;
	background-color: #232323;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	background-image: none;

	@include media(xs) {
		padding: 70px 20px 16px 20px;
	}
	@include media(sm) {
		flex-direction: row;
		padding: 30px 50px 16px 50px;
		background-image: url('../../images/backgrounds/header.jpg');
	}
	@include media(md) {
		padding: 33px 75px 18px 75px;
	}
	@include media(lg) {
		padding: 33px 100px 20px 100px;
	}

	&__companyName {
		font-family: 'Roboto', sans-serif;
		font-weight: bold;
		letter-spacing: 0.8px;
		text-align: left;
		color: #ffffff;
		font-size: 30px;

		@include media(xs) {

		}
		@include media(sm) {
			font-size: 32px;
		}
		@include media(md) {
			font-size: 39px;
		}
		@include media(lg) {

		}
	}

	&__companyLocation {
		font-family: 'Roboto', sans-serif;
		letter-spacing: 0.4px;
		text-align: left;
		color: #ffffff;
		margin-top: 12px;
		font-size: 16px;

		@include media(xs) {

		}
		@include media(sm) {
			font-size: 18px;
		}
		@include media(md) {
			font-size: 19.5px;
		}
		@include media(lg) {

		}
	}

	&__companyName,
	&__companyLocation {
		text-align: center;

		@include media(xs) {

		}
		@include media(sm) {
			text-align: left;
		}
		@include media(md) {

		}
		@include media(lg) {

		}
	}

	&__phone {
		font-family: 'Roboto', sans-serif;
		letter-spacing: 0.5px;
		color: #ffffff;
		font-size: 23px;
		margin-top: 20px;

		@include media(xs) {

		}
		@include media(sm) {
			margin-top: 10px;
		}
		@include media(md) {
			font-size: 24.5px;
		}
		@include media(lg) {

		}
	}

	&__mail {
		font-family: 'Roboto', sans-serif;
		letter-spacing: 0.4px;
		color: #ffffff;
		margin-top: 11px;
		font-size: 16px;

		@include media(xs) {

		}
		@include media(sm) {
		}
		@include media(md) {
			font-size: 19px;
		}
		@include media(lg) {

		}
	}

	&__phone,
	&__mail {
		text-align: right;

		@include media(xs) {

		}
		@include media(sm) {
		}
		@include media(md) {

		}
		@include media(lg) {

		}
	}
}

////////////////////////////// main block //////////////////////////////

.mainSection {
	padding: 30px 20px;
	background-color: #232323;
	background-image: url('../../images/backgrounds/main-section.jpg');
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	display: block;

	@include media(xs) {
		padding: 36px 20px;
	}
	@include media(sm) {
		padding: 50px 30px;
		margin-top: 0;
	}
	@include media(md) {
		padding: 60px 13%;
	}
	@include media(lg) {
		padding: 70px 13%;
	}

	&__title {
		font-family: 'Proxima Nova', sans-serif;
		font-weight: bold;
		letter-spacing: 1px;
		text-align: center;
		color: #f3d357;
		font-size: 30px;

		@include media(xs) {
			font-size: 35px;
		}
		@include media(sm) {
			font-size: 40px;
		}
		@include media(md) {
			font-size: 45px;
		}
		@include media(lg) {
			font-size: 50px;
		}
	}

	&__info {
		font-family: 'Roboto', sans-serif;
		letter-spacing: 0.5px;
		color: #ffffff;
		font-size: 16px;

		b {
			font-weight: bold;
			color: #f3d357;
		}

		@include media(xs) {
			font-size: 18px;
		}
		@include media(sm) {
			font-size: 20px;
		}
		@include media(md) {
			font-size: 23px;
			text-align: center;
		}
		@include media(lg) {
			font-size: 25px;
		}
	}
}

////////////////////////////// section services //////////////////////////////

.services {
	padding: 80px 0 70px 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;

	&__title {
		font-family: 'Proxima Nova', sans-serif;
		font-weight: bold;
		letter-spacing: 1px;
		text-align: center;
		color: #272727;
		margin: 0;
		text-transform: uppercase;
		font-size: 30px;

		@include media(xs) {
			font-size: 35px;
		}
		@include media(sm) {
			font-size: 40px;
		}
		@include media(md) {
			font-size: 45px;
		}
		@include media(lg) {
			font-size: 50px;
		}
	}

	&__info {
		margin-top: 40px;
		border: solid 3px #272727;
		padding: 18px;
		display: flex;
		justify-content: center;
		text-align: center;
		font-family: 'Roboto', sans-serif;
		font-size: 16px;
		line-height: 1.63;
		letter-spacing: 0.3px;
		color: #272727;
		width: 318px;

		@include media(xs) {

		}
		@include media(sm) {
			width: 460px;
		}
		@include media(md) {

		}
		@include media(lg) {

		}
	}

	&__orders {
		margin: 11px 0 0 0;
		padding: 0;
		list-style-type: none;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		width: 100%;
		max-width: 1100px;
	}

	&__note {
		font-family: 'Proxima Nova', sans-serif;
		font-size: 26px;
		font-weight: bold;
		line-height: 1.23;
		letter-spacing: 0.5px;
		text-align: center;
		color: #272727;
	}
}

