$xs-break: 576px;
$sm-break: 768px;
$md-break: 992px;
$lg-break: 1240px;

@mixin media($media) {
	@if $media == xs {
		@media screen and (min-width: #{$xs-break}) {
			@content;
		}
	} @else if $media == sm {
		@media screen and (min-width: #{$sm-break}) {
			@content;
		}
	} @else if $media == md {
		@media screen and (min-width: #{$md-break}) {
			@content;
		}
	} @else if $media == lg {
		@media screen and (min-width: #{$lg-break}) {
			@content;
		}
	} @else {
		@media screen and (min-width: #{$media}) {
			@content;
		}
	}
}