.iconText {
	margin: 40px 15px 0 15px;
	display: flex;
	flex-direction: column;
	align-items: center;

	&__icon {
		width: 150px;
		height: 150px;
	}

	&__text {
		font-family: 'Roboto', sans-serif;
		font-size: 18px;
		line-height: 1.44;
		letter-spacing: 0.4px;
		text-align: center;
		color: #272727;
	}
}