@import "../../scss/mixins/media";

.navbar {
	$root: &;

	width: 100%;
	display: flex;
	flex-direction: column;
	background-color: #ECECEB;
	overflow: hidden;
	max-height: 0;
	padding-top: 40px;
	transition: 200ms ease-out max-height;
	position: fixed;
	top: 0;

	@include media(xs) {

	}
	@include media(sm) {
		position: relative;
	}
	@include media(md) {

	}
	@include media(lg) {

	}

	&__mobileTitle {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		background-color: #2d2d2d;
		color: #ECECEB;
		height: 40px;
		font-family: 'Proxima Nova', sans-serif;
		text-transform: uppercase;
		font-weight: 600;
		font-size: 15px;
		align-items: center;
		justify-content: center;
		letter-spacing: 1.5px;
		display: flex;

		@include media(xs) {

		}
		@include media(sm) {
			display: none;
		}
		@include media(md) {

		}
		@include media(lg) {

		}
	}

	@include media(xs) {

	}
	@include media(sm) {
		height: 120px;
		max-height: none;
		flex-direction: row;
		padding-top: 0;
	}
	@include media(md) {
		height: 160px;
	}
	@include media(lg) {
		height: 200px;
	}

	&_open {
	  max-height: 236px;

		@include media(xs) {

		}
		@include media(sm) {
			max-height: none;
		}
		@include media(md) {

		}
		@include media(lg) {

		}
	}

	&__item {
		display: flex;
		flex: 1 1 auto;
		justify-content: center;
		align-items: center;
		text-decoration: none;
		padding: 10px 0;
		font-family: 'Proxima Nova', sans-serif;
		text-transform: uppercase;
		font-weight: 600;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.33;
		letter-spacing: 0.5px;
		text-align: left;
		color: #272727;
		font-size: 15px;

		@include media(xs) {
		}
		@include media(sm) {
			font-size: 13px;
			padding: 0;
		}
		@include media(md) {
			font-size: 18px;
		}
		@include media(lg) {
			font-size: 24px;
		}

		&:visited,
		&:focus,
		&:active {
			text-decoration: none;
			color: black;
		}
	}

	&__button {
		outline: none;
		background-color: transparent;
		border: none;
		position: absolute;
		padding: 0;
		margin: 5px;
		top: 0;
		right: 0;
		height: 30px;
		width: 30px;
		cursor: pointer;
		display: block;

		&_active {
		  #{$root}__buttonLine {
			  &:nth-child(1),
			  &:nth-child(2),
			  &:nth-child(3) {
					top: 13px;
			  }

			  &:nth-child(1) {
		      transform: rotateZ(45deg);
		    }

			  &:nth-child(2) {
				  transform: rotateZ(-45deg);
			  }

			  &:nth-child(3) {
					display: none;
			  }
		  }
		}

		@include media(xs) {

		}
		@include media(sm) {
			display: none;
		}
		@include media(md) {
		}
		@include media(lg) {

		}
	}

	&__buttonLine {
		display: block;
		height: 4px;
		width: 24px;
		border-radius: 6px;
		background-color: #ffffff;
		position: absolute;

		&:nth-child(1),
		&:nth-child(2),
		&:nth-child(3) {
			left: 2px;
			transition: top 200ms ease-out, transform 200ms ease-out;
		}


		&:nth-child(1) {
			top: 4px;
		}

		&:nth-child(2) {
			top: 12px;
		}

		&:nth-child(3) {
			top: 20px;
		}

	}
}